import React from 'react';
import oppstyles from '../../styles/opportunity.module.scss'

export default class UpcomingOpportunity extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            status: '',
        };
    }
    render() {
        const backgroundColor = this.props.backgroundColor

        return (<div className={oppstyles.gridElement}>
            <div>
                <div>
        <div className={oppstyles.headline}><h4 className={oppstyles.h4}>{this.props.name}</h4> <p className={oppstyles.button} style={{backgroundColor: backgroundColor}}>{this.props.status}</p></div>
                    <p className={oppstyles.postedBy}>posted by <a className={oppstyles.a} href="/">{this.props.creator}</a></p>
                </div>
                <div>
                    <p className={oppstyles.postedBy}><br />{this.props.startTime} – {this.props.endTime} <br /> {this.props.date} / {this.props.location}<br /></p>
                    <p className={oppstyles.desc}>{this.props.desc}</p>
                    <p className={oppstyles.postedBy}>Participants: {this.props.totalNumberOfAccepted} / {this.props.numberOfParticipantsWanted}</p>
                    <div className={oppstyles.discover}><a className={[oppstyles.aDesc, oppstyles.a].join(' ')} href="/">Read More</a> <p className={oppstyles.icon}><i className={[oppstyles.arrow, oppstyles.right].join(' ')}></i></p></div>
                </div>
            </div>
        </div>)
    }
}