import React from 'react';
import { addOrganization } from '../../Utils/networking'
import styles from './RegistrationForm.module.scss'
import { ReactComponent as Illustation } from './Team-Work.svg';

export default class RegistrationForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            contactFirstName: '',
            contactLastName: '',
            email: '',
            organizationName: '',
            phoneNumber: '',
            street: '',
            houseNumber: '',
            city: '',
            zipCode: '',
            password: '',
            error: false,
            errorMsg: '',
            display: 'none',
            displayForm: 'block'
        }

        this.form = React.createRef();
        this.handleChange = this.handleChange.bind(this);
        this.validate = this.validate.bind(this);
    }

    validate() {
        return this.form.current.reportValidity();
    }

    handleChange(event) {
        this.setState({
            [event.target.id]: event.target.value
        });
    };

    handleAddButtonClicked(e) {
        e.preventDefault();
        const isValid = this.validate();
        const newUser = {
            contactFirstName: this.state.contactFirstName,
            contactLastName: this.state.contactLastName,
            organizationName: this.state.organizationName,
            email: this.state.email,
            password: this.state.password,
            phoneNumber: this.state.phoneNumber,
            street: this.state.street,
            houseNumber: this.state.houseNumber,
            city: this.state.city,
            zipCode: this.state.zipCode,
        }

        if (isValid) {
            addOrganization(newUser, (organizationList, response) => {
                if (organizationList) {
                    this.setState({
                        error: false,
                        display: 'block',
                        displayForm: 'none'
                    })
                } else {
                    this.setState({
                        error: true,
                        errorMsg: response
                    })
                }
            });
            console.log('Sent!')
        } else {
            // this.setState({
            //     error: true,
            //     errorM
            // })
            //Take care of errors from the clients side.... with the validator??? check homework html5 form validation
            console.log("Client Side Form Validation error! ")
        }
    };

    render() {
        const takenEmailErrorMsg = this.state.errorMsg.includes("Email is already taken") ? "Email is already taken." : ""
        return (<div className={styles.page}>
            <div className={styles.formDiv}>
                <h3>Register <br />as organization</h3>
                <div className={styles.alert} style={{ display: this.state.display }}>
                    <p>Hello, {this.state.contactFirstName}!</p>
                    <p>You have successfully signed up to Apptruistic! You can now log in with your email and password!</p>
                </div>
                <form style={{ display: this.state.displayForm }} action="http://localhost:8080/register/organization" autoComplete="off" method="POST" ref={this.form}>
                    <div className={styles.formWrapper}>
                        <input
                            id="organizationName"
                            placeholder='Organization Name*'
                            aria-label="Organization Name"
                            type="text"
                            name="organizationName"
                            value={this.state.organizationName}
                            onChange={this.handleChange}
                            required />
                        <input
                            id="contactFirstName"
                            placeholder='Contact Person First Name*'
                            aria-label="Contact Person First Name"
                            type="text"
                            name="contactFirstName"
                            value={this.state.contactFirstName}
                            onChange={this.handleChange}
                            required />
                        <input
                            id="contactLastName"
                            placeholder="Contact Person Last Name*"
                            aria-label="Contact Person Last Name"
                            type="text"
                            name="contactLastName"
                            value={this.state.contactLastName}
                            onChange={this.handleChange}
                            required />
                        <div className={styles.divider}>
                            <input
                                id="street"
                                placeholder="Street name*"
                                aria-label="Street name"
                                type="text"
                                name="street"
                                value={this.state.street}
                                onChange={this.handleChange}
                                required />
                            <input
                                id="houseNumber"
                                className={styles.houseNumber}
                                placeholder="Nr.*"
                                aria-label="House number"
                                type="text"
                                name="houseNumber"
                                value={this.state.houseNumber}
                                onChange={this.handleChange}
                                required />
                        </div>
                        <div className={styles.divider}>
                            <input
                                id="zipCode"
                                placeholder="Zip code*"
                                aria-label="Zip code"
                                type="number"
                                name="zipCode"
                                value={this.state.zipCode}
                                onChange={this.handleChange}
                                required />
                            <input
                                id="city"
                                placeholder="City*"
                                aria-label="City"
                                type="text"
                                name="city"
                                value={this.state.city}
                                onChange={this.handleChange}
                                required />
                        </div>
                        <input
                            id="phoneNumber"
                            placeholder="Phone number"
                            aria-label="Phone number"
                            type="text"
                            name="phoneNumber"
                            value={this.state.phoneNumber}
                            onChange={this.handleChange}
                        />
                        <input
                            id="password"
                            placeholder="Password*"
                            aria-label="Password"
                            type="password"
                            name="password"
                            value={this.state.password}
                            onChange={this.handleChange}
                            minLength="8"
                            required />
                        <input
                            id="email"
                            placeholder="Organization's Email*"
                            aria-label="Organization's Email"
                            type="email"
                            name="email"
                            className={styles.email}
                            value={this.state.email}
                            onChange={this.handleChange}
                            required />
                    </div>
                    {this.state.error === true ? <p className={styles.error}>{takenEmailErrorMsg}</p> : ""}
                    <button type="submit"
                        onClick={e => this.handleAddButtonClicked(e)}
                    >Sign up</button>
                </form>
            </div>
            <div className={styles.imgContainer}><Illustation /></div>
        </div>
        );
    }
}

