import React from 'react';
import './Tabs.scss'
import { NavLink } from 'react-router-dom'

const SelectionTabsRegister = () => {

    return (<div className='selectionNav'>
        <nav className='nav'><span className='span'>Register as</span>
        {/* activeClassName only working with NavLink and it adds a css rule to the active link */}
            <NavLink className='a' activeClassName='selected' to="/register-volunteer">Volunteer</NavLink>
            <NavLink className='a' activeClassName='selected' to="/register-individual">Individual</NavLink>
            <NavLink className='a' activeClassName='selected' to="/register-organization">Organization</NavLink>
            </nav>
    </div>
    )
}

export default SelectionTabsRegister