import React from 'react';
import { addIndividual } from '../../Utils/networking'
import styles from './RegistrationForm.module.scss'
import { ReactComponent as Illustation } from './Team-Work.svg';

export default class RegistrationForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            firstName: '',
            lastName: '',
            email: '',
            dateOfBirth: '',
            phoneNumber: '',
            street: '',
            houseNumber: '',
            city: '',
            zipCode: '',
            day: '',
            month: '',
            year: '',
            gender: '',
            password: '',
            error: false,
            errorMsg: '',
            display: 'none',
            displayForm: 'block'
        }

        this.form = React.createRef();
        this.handleChange = this.handleChange.bind(this);
        this.validate = this.validate.bind(this);
    }

    //Form Validation API throws a true if it's valid and a 'false' if it's invalid
    validate() {
        return this.form.current.reportValidity();
    }

    handleChange(event) {
        this.setState({
            [event.target.id]: event.target.value
        });
        if (this.state.day && this.state.month && this.state.year) {
            const birthDateParsed = `${this.state.day}.${this.state.month}.${this.state.year}`;
            this.setState({ dateOfBirth: birthDateParsed })
        }
    };



    //OnSubmit:
    handleAddButtonClicked(e) {
        e.preventDefault();
        const categoryUppercase = this.state.category.toUpperCase()
        const isValid = this.validate();
        const newUser = {
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            email: this.state.email,
            dateOfBirth: this.state.dateOfBirth,
            gender: this.state.gender,
            password: this.state.password,
            phoneNumber: this.state.phoneNumber,
            street: this.state.street,
            houseNumber: this.state.houseNumber,
            city: this.state.city,
            zipCode: this.state.zipCode,
        }

        if (isValid) {
            addIndividual(newUser, (individualList, response) => {
                if (individualList) {
                    this.setState({
                        error: false,
                        display: 'block',
                        displayForm: 'none'
                    })
                } else {
                    this.setState({
                        error: true,
                        errorMsg: response
                    })
                }
            });
        } else {
            // this.setState({
            //     error: true,
            //     errorM
            // })
            //Take care of errors from the clients side.... with the validator??? check homework html5 form validation
            console.log("Client Side Form Validation error! ")
        }
    };

    render() {
        const takenEmailErrorMsg = this.state.errorMsg.includes("Email is already taken") ? "Email is already taken." : ""
        return (<div className={styles.page}>
            <div className={styles.formDiv}>
                <h3>Register <br />as individual</h3>
                <div className={styles.alert} style={{ display: this.state.display }}>
                    <p>Hello, {this.state.firstName}!</p>
                    <p>You have successfully signed up to Apptruistic! You can now log in with your email and password!</p>
                </div>
                <form style={{ display: this.state.displayForm }} action="http://localhost:8080/register/individual" autoComplete="off" method="POST" ref={this.form}>
                    <div className={styles.formWrapper}>
                        <input
                            id="firstName"
                            placeholder='First Name*'
                            aria-label="First Name"
                            type="text"
                            name="firstName"
                            value={this.state.firstName}
                            onChange={this.handleChange}
                            required />
                        <input
                            id="lastName"
                            placeholder="Last Name*"
                            aria-label="Last Name"
                            type="text"
                            name="lastName"
                            value={this.state.lastName}
                            onChange={this.handleChange}
                            required />
                        <div className={styles.divider}>
                            <input
                                pattern="0[1-9]|[12][0-9]|3[01]"
                                id="day"
                                placeholder="DD"
                                aria-label="Day"
                                type="text"
                                name="day"
                                onChange={this.handleChange}
                            />
                            <input
                                pattern="0[1-9]|[12][0-12]"
                                id="month"
                                placeholder="MM"
                                aria-label="Month"
                                type="text"
                                name="month"
                                onChange={this.handleChange}
                            />
                            <input
                                className={styles.inputText}
                                id="year"
                                placeholder="YYYY"
                                aria-label="Year"
                                type="number"
                                name="year"
                                min="1900"
                                max="2002"
                                onChange={this.handleChange}
                            />
                        </div>
                        <div className={styles.divider}>
                            <input
                                id="street"
                                placeholder="Street name*"
                                aria-label="Street name"
                                type="text"
                                name="street"
                                value={this.state.street}
                                onChange={this.handleChange}
                                required />
                            <input
                                id="houseNumber"
                                className={styles.houseNumber}
                                placeholder="Nr.*"
                                aria-label="House number"
                                type="text"
                                name="houseNumber"
                                value={this.state.houseNumber}
                                onChange={this.handleChange}
                                required />
                        </div>
                        <div className={styles.divider}>
                            <input
                                id="zipCode"
                                placeholder="Zip code*"
                                aria-label="Zip code"
                                type="number"
                                name="zipCode"
                                value={this.state.zipCode}
                                onChange={this.handleChange}
                                required />
                            <input
                                id="city"
                                placeholder="City*"
                                aria-label="City"
                                type="text"
                                name="city"
                                value={this.state.city}
                                onChange={this.handleChange}
                                required />
                        </div>
                        <input
                            id="phoneNumber"
                            placeholder="Phone number"
                            aria-label="Phone number"
                            type="text"
                            name="phoneNumber"
                            value={this.state.phoneNumber}
                            onChange={this.handleChange}
                        />
                        <input type="text" id="gender" name="gender" list="genderList" placeholder="Select your gender"
                            pattern="[Ff]emale|[Mm]ale|[Uu]nspecified" onChange={this.handleChange} />
                        <datalist id="genderList">
                            <option>Female</option>
                            <option>Male</option>
                            <option>Unspecified</option>
                        </datalist>
                        <input
                            id="password"
                            placeholder="Password*"
                            aria-label="Password"
                            type="password"
                            name="password"
                            value={this.state.password}
                            onChange={this.handleChange}
                            minLength="8"
                            required />
                        <input
                            id="email"
                            placeholder="E-Mail*"
                            aria-label="E-Mail"
                            type="email"
                            name="email"
                            className={styles.email}
                            value={this.state.email}
                            onChange={this.handleChange}
                            required />
                    </div>
                    {this.state.error === true ? <p className={styles.error}>{takenEmailErrorMsg}</p> : ""}
                    <button type="submit"
                        onClick={e => this.handleAddButtonClicked(e)}
                    >Sign up</button>
                </form>
            </div>
            <div className={styles.imgContainer}><Illustation /></div>
        </div>
        );
    }
}

