import React from 'react'
import styles from './LogIn.module.scss'
import { ReactComponent as Illustation } from '../Registration/Support-team.svg';
import AuthService from "../../services/auth.service";
import { withRouter } from 'react-router';

class LogIn extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: '',
            loading: false,
            message: ''
        }

        this.form = React.createRef();
        this.handleLogin = this.handleLogin.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.validate = this.validate.bind(this);
    }

    //Form Validation API throws a true if it's valid and a 'false' if it's invalid
    validate() {
        return this.form.current.reportValidity();
    }

    handleChange(event) {
        this.setState({
            [event.target.id]: event.target.value
        });
   };

    //OnSubmit:
    handleLogin(e) {
        e.preventDefault();
        this.setState({
            message: "",
            loading: true,
            error: false
        });

        const isValid = this.validate();
        if (isValid) {
            AuthService.login(this.state.email, this.state.password).then(
                () => {
                    this.props.history.push("/dashboard");
                    window.location.reload();
                    this.setState({
                        loading: false,
                        error: false
                    });
                },
                error => {
                    const resMessage =
                        (error.response &&
                            error.response.data &&
                            error.response.data.message) ||
                        error.message ||
                        error.toString();

                    this.setState({
                        loading: false,
                        message: resMessage,
                        error: true
                    });
                    console.log('USER LOGIN: ERROR RECEIVED!', resMessage)
                }
            );
        } else {
            this.setState({
                loading: false
            });
        }
    };

    render() {
        return (<div className={styles.page}>
            <div className={styles.formDiv}>
                <h3>Log in</h3>
                <form onSubmit={this.handleLogin} ref={this.form}>
                    <div className={styles.formWrapper}>
                        <input
                            id="email"
                            placeholder='Email'
                            aria-label="email"
                            type="email"
                            name="email"
                            onChange={this.handleChange}
                            required />
                        <input
                            id="password"
                            placeholder="Password"
                            aria-label="password"
                            type="password"
                            name="password"
                            onChange={this.handleChange}
                            required />
                    </div>
                    {this.state.error === true ? <p className={styles.error}>Password or email is wrong. Please try again.</p> : ""}
                    <button type="submit"
                        // onClick={e => this.handleAddButtonClicked(e)}
                    >Log in</button>
                </form>
            </div>
            <div className={styles.imgContainer}><Illustation /></div>
        </div>
        );
    }
}

export default withRouter(LogIn);