import React from 'react';
import styles from '../Dashboard/Dashboard.module.scss';
import profileStyles from './Profile.module.scss';
import { getUser } from '../../Utils/networking';
import AuthService from "../../services/auth.service";
import { Link } from 'react-router-dom';
import { updateVolunteer } from '../../Utils/networking';

export default class Dashboard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentUser: AuthService.getCurrentUser(),
            gender: '',
            firstName: '',
            lastName: '',
            email: '',
            phoneNumber: '',
            password: '',
            error: false,
            errorMsg: '',
            personalDescription: '',
            display: 'none',
            disabled: true,
        };

        this.form = React.createRef();
        this.handleChange = this.handleChange.bind(this);
        this.validate = this.validate.bind(this);
        this.handleClickEdit = this.handleClickEdit.bind(this)
        this.logOut = this.logOut.bind(this);
    }

    logOut() {
        AuthService.logout();
    }

    validate() {
        return this.form.current.reportValidity();
    }

    handleChange(event) {
        this.setState({
            [event.target.id]: event.target.value
        });
    };


    componentDidMount() {
        const token = this.state.currentUser.accessToken
        const email = this.state.currentUser.email

        getUser(token, email, (user, responseMsg) => {
            if (user) {
                this.setState({
                    firstName: user.firstName,
                    lastName: user.lastName,
                    email: user.email,
                    phoneNumber: user.phoneNumber,
                    password: user.password,
                    displayInputVolunteer: 'none',
                    displayInput: 'none',
                    gender: user.gender
                });
                return;
            }
            else {

                return;
            }
        });
    }

    handleClickEdit(e) {
        e.preventDefault();
        this.setState({
            disabled: false,
            display: 'block'
        });
    }

    handleAddButtonClicked(e) {
        const token = this.state.currentUser.accessToken
        const email = this.state.currentUser.email

        e.preventDefault();
        this.setState({
            disabled: true,
            display: 'none'
        });
        const isValid = this.validate();

        const updatedVolunteer = {
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            email: this.state.email,
            password: this.state.password,
            phoneNumber: this.state.phoneNumber,
            personalDescription: this.state.personalDescription,
            gender: this.state.gender
        };

        if (isValid) {
            updateVolunteer(updatedVolunteer, token, email, (organization, response) => {
                if (organization) {
                    this.setState({
                        error: false,
                    })
                } else {
                    this.setState({
                        error: true,
                        errorMsg: response
                    })
                }
            });
            console.log('Sent!')
        } else {
            console.log("Client Side Form Validation error! ")
        }

    }

    render() {

        const firstAndLastName = `${this.state.firstName} ${this.state.lastName}`;
        return (<div className={styles.dashboard}>
            <div className={styles.headlineAndButton}>
                <div>
                    <h3 className={styles.h3}>Profile</h3>
                    <p>{firstAndLastName} <Link className={profileStyles.a} to='/' onClick={this.logOut}>Log out</Link></p>
                </div>
                <button className={styles.button} onClick={this.handleClickEdit}>Edit Profile</button>
            </div>
            <section className={styles.opportunitiesWrapper}>
                <h4 className={styles.h4}>Your Data</h4>
                <section className={profileStyles.profile}>
                    <form action="http://localhost:8080/register/organization" autoComplete="off" method="POST" ref={this.form}>
                        <div className={profileStyles.profileWrapper}>
                            <label
                                for="firstName">First Name
            <input
                                    id="firstName"
                                    placeholder={this.state.firstName}
                                    aria-label="First Name"
                                    type="text"
                                    name="contactFirstName"
                                    value={this.state.firstName}
                                    onChange={e => this.handleChange(e)}
                                    required
                                    disabled={this.state.disabled} />
                            </label>
                            <label for="lastName">Last Name
            <input
                                    id="lastName"
                                    placeholder={this.state.lastName}
                                    aria-label="Last Name"
                                    type="text"
                                    name="lastName"
                                    value={this.state.lastName}
                                    onChange={e => this.handleChange(e)}
                                    required
                                    disabled={this.state.disabled} />
                            </label>
                    
                            <label for="phoneNumber">Phone Number
            <input
                                    id="phoneNumber"
                                    placeholder={this.state.phoneNumber}
                                    aria-label="Phone number"
                                    type="text"
                                    name="phoneNumber"
                                    value={this.state.phoneNumber}
                                    onChange={e => this.handleChange(e)}
                                    disabled={this.state.disabled}
                                />
                            </label>
                            <label for="password">Password
            <input
                                    id="password"
                                    placeholder={this.state.password}
                                    aria-label="Password"
                                    type="password"
                                    name="password"
                                    value={this.state.password}
                                    onChange={this.state.callback}
                                    minLength="8"
                                    required
                                    disabled />
                            </label>
                            <label for="email">Email
            <input
                                    id="email"
                                    placeholder={this.state.email}
                                    aria-label="Organization's Email"
                                    type="email"
                                    name="email"
                                    className={profileStyles.email}
                                    value={this.state.email}
                                    onChange={e => this.handleChange(e)}
                                    required
                                    disabled />
                            </label>
                            <label for="gender">Gender
            <input type="text" id="gender" name="gender" list="genderList" placeholder={this.state.gender}
                                    pattern="[Ff]emale|[Mm]ale|[Uu]nspecified" onChange={e => this.handleChange(e)} disabled={this.state.disabled} />
                                <datalist id="genderList">
                                    <option>Female</option>
                                    <option>Male</option>
                                    <option>Unspecified</option>
                                </datalist>
                            </label>
                        </div>
                        <label for="personalDescription">Personal Description
                        <textarea
                                id="personalDescription"
                                placeholder="Write something about yourself!"
                                aria-label="Write something about yourself!"
                                name="email"
                                value={this.state.personalDescription}
                                onChange={e => this.handleChange(e)}
                                disabled={this.state.disabled} />
                        </label>
                        <button type="submit"
                            className={styles.button} style={{ display: this.state.display }}
                            onClick={e => this.handleAddButtonClicked(e)}
                        >Save</button>
                    </form>
                </section>
            </section>
        </div>)
    }
}


