import React from 'react';
import styles from './Dashboard.module.scss'
import { getOrganization, getIndividual } from '../../Utils/networking'
import AuthService from "../../services/auth.service";
import { Link } from 'react-router-dom'
import PostedOpportunities from './PostedOpportunities'


export default class Dashboard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentUser: AuthService.getCurrentUser(),
            organizationName: '',
            firstName: '',
        };
    }


    componentDidMount() {
        const token = this.state.currentUser.accessToken
        const email = this.state.currentUser.email

        getOrganization(token, email, (user, responseMsg) => {
            if (user) {
                this.setState({
                    organizationName: user.organizationName,
                });
                return;
            }
            else {
                return;
            }
        });

        getIndividual(token, email, (user, responseMsg) => {
            if (user) {
                this.setState({
                    firstName: user.firstName,
                });
                return;
            }
            else {

                return;
            }
        });
    }


    render() {
        const isOrganizationOrIndividual = this.state.organizationName ? this.state.organizationName : this.state.firstName
        return (<div className={styles.dashboard}>
            <div className={styles.headlineAndButton}>
                <div>
                    <h3 className={styles.h3}>Dashboard</h3>
                    <p>Welcome, {isOrganizationOrIndividual}! <Link className={styles.a} to='/' onClick={this.logOut}>Log out</Link></p>
                </div>
                <Link to='/create-opportunity'><button className={styles.button}>Create Opportunity</button></Link>
            </div>
            <section className={styles.opportunitiesWrapper}>
                <h4 className={styles.h4}>Your Opportunities</h4>
                <PostedOpportunities />
            </section>
        </div>)
    }
}


