import React from 'react';
import styles from '../Registration/RegistrationForm.module.scss'
import { ReactComponent as Illustation } from '../Registration/Team.svg';
import AuthService from "../../services/auth.service";
import { getOrganization, getIndividual, addOpportunity } from '../../Utils/networking'
import { Link } from 'react-router-dom'


export default class CreateOpportunity extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentUser: AuthService.getCurrentUser(),
            name: '',
            firstName: '',
            lastName: '',
            startMinutes: '',
            startTime: '',
            shortDescription: '',
            detailedDescription: '',
            organizationName: '',
            individualName: '',
            category: '',
            occurDate: '',
            endTime: '',
            numberOfParticipants: '',
            street: '',
            houseNumber: '',
            city: 'Vienna',
            zipCode: '',
            id: '',
            error: false,
            errorMsg: '',
            display: 'none',
            displayForm: 'block',
            creatorType: '',
            creatorId: '',
            creatorName: '',
        }

        this.form = React.createRef();
        this.handleChange = this.handleChange.bind(this);
        this.validate = this.validate.bind(this);
    }

    //Form Validation API throws a true if it's valid and a 'false' if it's invalid
    validate() {
        return this.form.current.reportValidity();
    }

    handleChange(event) {
        this.setState({
            [event.target.id]: event.target.value
        });
        if (this.state.day && this.state.month && this.state.year) {
            const birthDateParsed = `${this.state.day}.${this.state.month}.${this.state.year}`;
            this.setState({ occurDate: birthDateParsed })
        }
    };

    componentDidMount() {
        const token = this.state.currentUser.accessToken
        const email = this.state.currentUser.email

        //if condition so you don't ask for both information
        getOrganization(token, email, (user, responseMsg) => {
            if (user) {
                this.setState({
                    organizationName: user.organizationName,
                    creatorType: "ORGANIZATION",
                    id: user.id
                });
                return;
            }
            else {
                //Errors
                return;
            }
        });

        getIndividual(token, email, (user, responseMsg) => {
            if (user) {
                this.setState({
                    firstName: user.firstName,
                    lastName: user.lastName,
                    creatorType: "INDIVIDUAL",
                    id: user.id,
                });
                return;
            }
            else {
                //Errors
                return;
            }
        });
    }


    //OnSubmit:
    handleAddButtonClicked(e) {
        e.preventDefault();
        const token = this.state.currentUser.accessToken
        const isValid = this.validate();
        const categoryUppercase = this.state.category.toUpperCase()
        const firstAndLastNameIndividual = `${this.state.firstName} ${this.state.lastName}`
        const isOrganizationOrIndividual = this.state.organizationName ? this.state.organizationName : firstAndLastNameIndividual
        const state = this.state;
        const newOpportunity = {
            name: state.name,
            category: categoryUppercase,
            creatorType: state.creatorType,
            creatorId: state.id,
            creatorName: `${isOrganizationOrIndividual}`,
            shortDescription: state.shortDescription,
            detailedDescription: state.detailedDescription,
            occurDate: state.occurDate,
            startTime: state.startTime,
            endTime: state.endTime,
            numberOfParticipants: state.numberOfParticipants,
            street: state.street,
            houseNumber: state.houseNumber,
            city: state.city,
            zipCode: state.zipCode
        }

        if (isValid) {
            addOpportunity(newOpportunity, token, (volunteerList, response) => {
                if (volunteerList) {
                    this.setState({
                        error: false,
                        display: 'block',
                        displayForm: 'none'
                    })
                } else {
                    this.setState({
                        error: true,
                        errorMsg: response
                    })
                }
            });

        } else {
            console.log("Client Side Form Validation error! ")
        }
    };

    render() {
        return (<div className={styles.page}>
            <div className={[styles.formDiv, styles.formOpportunity].join(' ')}>
                <h3>Create<br /> Opportunity</h3>
                <div className={styles.alert} style={{ display: this.state.display }}>
                    <p>You have successfully created an opportunity! You can view all your opportunities in your <Link to='/dashboard'>dashboard</Link>.
                        </p>
                </div>
                <form style={{ display: this.state.displayForm }} action="http://localhost:8080/volunteers" autoComplete="off" method="POST" ref={this.form}>
                    <div className={styles.formWrapper}>
                        <input
                            id="name"
                            placeholder='Opportunity title*'
                            aria-label="Title"
                            type="text"
                            name="name"
                            value={this.state.name}
                            onChange={this.handleChange}
                            required />
                        <div className={styles.divider}>
                            <input className={styles.category} type="text" id="category" name="category" list="categoryList" required placeholder="Category*"
                                pattern="Household|Environment|Animals|Refugees|Caretaking|Transport|ITSupport|Teaching" onChange={this.handleChange} />
                            <datalist id="categoryList">
                                <option>Household</option>
                                <option>Environment</option>
                                <option>Animals</option>
                                <option>Refugees</option>
                                <option>Caretaking</option>
                                <option>Transport</option>
                                <option>ITSupport</option>
                                <option>Teaching</option>
                            </datalist>
                            <input
                                className={styles.numberOfParticipants}
                                id="numberOfParticipants"
                                placeholder="Needed Volunteers (Number)"
                                aria-label="Number of volunteers needed"
                                type="number"
                                name="numberOfParticipants"
                                onChange={this.handleChange}
                                min="0"
                                required
                            />
                        </div>
                        <textarea
                            id="shortDescription"
                            placeholder="Short Description (max. 280 characters)*"
                            aria-label="Short Description*"
                            type="text"
                            rows="4"
                            name="shortDescription"
                            value={this.state.shortDescription}
                            onChange={this.handleChange}
                            maxlength="280"
                            required></textarea>
                        <textarea
                            id="detailedDescription"
                            placeholder="Detailed Description (max. 2000 characters)*"
                            aria-label="Detailed Description*"
                            type="text"
                            rows="4"
                            name="detailedDescription"
                            value={this.state.detailedDescription}
                            onChange={this.handleChange}
                            maxlength="2000"
                            required></textarea>
                        <p className={styles.p}>Please enter the start date in following format:</p>
                        <div className={styles.divider}>
                            <input
                                pattern="0[1-9]|[12][0-9]|3[01]"
                                id="day"
                                placeholder="(DD)*"
                                aria-label="Day"
                                type="text"
                                name="day"
                                onChange={this.handleChange}
                                required />
                            <input
                                pattern="0[1-9]|[12][0-12]"
                                id="month"
                                placeholder="(MM)*"
                                aria-label="Month"
                                type="text"
                                name="month"
                                onChange={this.handleChange}
                                required />
                            <input
                                className={styles.inputText}
                                id="year"
                                placeholder="(YYYY)*"
                                aria-label="Year"
                                type="number"
                                name="year"
                                min="2020"
                                max="2100"
                                onChange={this.handleChange}
                                required />
                        </div>
                        <div className={styles.pDivider}>
                            <p className={styles.p}>Please enter the start time:</p>
                            <p className={[styles.p]}>Please enter the end time:</p>
                        </div>
                        <div className={styles.divider}>
                            <input
                                pattern="([01]?[0-9]|2[0-3]):[0-5][0-9]"
                                id="startTime"
                                placeholder="10:00"
                                aria-label="Start: Hour"
                                type="text"
                                name="startTime"
                                onChange={this.handleChange}
                                required />
                            <input
                                pattern="([01]?[0-9]|2[0-3]):[0-5][0-9]"
                                id="endTime"
                                placeholder="18:00"
                                aria-label="End: Hour*"
                                type="text"
                                name="endTime"
                                onChange={this.handleChange}
                                required />
                        </div>
                        <div className={styles.divider}>
                            <input
                                id="street"
                                placeholder="Street name*"
                                aria-label="Street name*"
                                type="text"
                                name="street"
                                value={this.state.street}
                                onChange={this.handleChange}
                                required />
                            <input
                                id="houseNumber"
                                className={styles.houseNumber}
                                placeholder="Nr.*"
                                aria-label="House number*"
                                type="text"
                                name="houseNumber"
                                value={this.state.houseNumber}
                                onChange={this.handleChange}
                                required />
                        </div>
                        <div className={styles.divider}>
                            <input
                                id="zipCode"
                                placeholder="Zip code*"
                                aria-label="Zip code*"
                                type="number"
                                name="zipCode"
                                value={this.state.zipCode}
                                onChange={this.handleChange}
                                required />
                            <input
                                id="city"
                                placeholder="City*"
                                aria-label="City*"
                                type="text"
                                name="city"
                                value='Vienna'
                                onChange={this.handleChange}
                                required
                                disabled />
                        </div>
                    </div>
                    {this.state.error === true ? <p className={styles.error}>{}</p> : ""}
                    <button type="submit"
                        onClick={e => this.handleAddButtonClicked(e)}
                    >Create Opportunity</button>
                </form>
            </div>
            <div className={styles.imgContainer}>
                <Illustation /></div>
        </div>
        );
    }
}

