import React from 'react';
import styles from '../Dashboard/Dashboard.module.scss';
import profileStyles from './Profile.module.scss';
import { getOrganization, getIndividual, getUser } from '../../Utils/networking';
import AuthService from "../../services/auth.service";
import { Link } from 'react-router-dom';
import { updateOrganization, updateVolunteer, updateIndividual } from '../../Utils/networking';
import { ProfileOrganization, ProfileIndividual, ProfileVolunteer } from './Profiles.js'

export default class Dashboard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentUser: AuthService.getCurrentUser(),
            preferredType: '',
            organizationName: '',
            gender: '',
            contactFirstName: '',
            contactLastName: '',
            firstName: '',
            lastName: '',
            email: '',
            phoneNumber: '',
            street: '',
            houseNumber: '',
            city: '',
            zipCode: '',
            password: '',
            error: false,
            errorMsg: '',
            personalDescription: '',
            display: 'none',
            disabled: true,
            displayInput: '',
            displayForm: 'block'
        };

        this.form = React.createRef();
        // this.handleChange = this.handleChange.bind(this);
        this.validate = this.validate.bind(this);
        this.handleClickEdit = this.handleClickEdit.bind(this)
        this.logOut = this.logOut.bind(this);
    }

    logOut() {
        AuthService.logout();
    }

    validate() {
        return this.form.current.reportValidity();
    }

    handleChange = (event) => {
        if (event.target.id === 'preferredType') {
            this.setState({
                preferredType: event.target.value.toUpperCase()
            })
        } else {
        this.setState({
            [event.target.id]: event.target.value
        });
    }
    };


    componentDidMount() {
        const token = this.state.currentUser.accessToken
        const email = this.state.currentUser.email
        const roles = this.state.currentUser.roles

        if (roles.includes("ROLE_ORGANIZATION")) {
            getOrganization(token, email, (user, responseMsg) => {
                if (user) {
                    this.setState({
                        organizationName: user.organizationName,
                        contactFirstName: user.contactFirstName,
                        contactLastName: user.contactLastName,
                        email: user.email,
                        phoneNumber: user.phoneNumber,
                        street: user.street,
                        houseNumber: user.houseNumber,
                        city: user.city,
                        zipCode: user.zipCode,
                        password: user.password,
                        displayInput: 'block',
                        personalDescription: user.personalDescription
                    });
                    return;
                }
                else {
                    return;
                }
            });
        }


        if (roles.includes("ROLE_INDIVIDUAL")) {
            getIndividual(token, email, (user, responseMsg) => {
                if (user) {
                    this.setState({
                        firstName: user.firstName,
                        lastName: user.lastName,
                        email: user.email,
                        phoneNumber: user.phoneNumber,
                        street: user.street,
                        houseNumber: user.houseNumber,
                        city: user.city,
                        zipCode: user.zipCode,
                        password: user.password,
                        displayInput: 'none',
                        personalDescription: user.personalDescription
                    });
                    return;
                }
                else {

                    return;
                }
            });
        }


        if (roles.includes("ROLE_VOLUNTEER")) {
            getUser(token, email, (user, responseMsg) => {
                if (user) {
                    this.setState({
                        firstName: user.firstName,
                        lastName: user.lastName,
                        email: user.email,
                        phoneNumber: user.phoneNumber,
                        password: user.password,
                        displayInputVolunteer: 'none',
                        displayInput: 'none',
                        gender: user.gender,
                        preferredType: user.preferredType,
                        personalDescription: user.personalDescription
                    });
                    return;
                }
                else {

                    return;
                }
            });
        }
    }

    handleClickEdit(e) {
        e.preventDefault();
        this.setState({
            disabled: false,
            display: 'block'
        });
    }

    handleAddButtonClicked(e) {
        e.preventDefault();
        this.setState({
            disabled: true,
            display: 'none'
        });
        const isValid = this.validate();
        const updatedOrganization = {
            contactFirstName: this.state.contactFirstName,
            contactLastName: this.state.contactLastName,
            organizationName: this.state.organizationName,
            email: this.state.email,
            password: this.state.password,
            phoneNumber: this.state.phoneNumber,
            street: this.state.street,
            houseNumber: this.state.houseNumber,
            city: this.state.city,
            zipCode: this.state.zipCode,
            personalDescription: this.state.personalDescription
        };

        const updatedIndividual = {
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            email: this.state.email,
            password: this.state.password,
            phoneNumber: this.state.phoneNumber,
            street: this.state.street,
            houseNumber: this.state.houseNumber,
            city: this.state.city,
            zipCode: this.state.zipCode,
            personalDescription: this.state.personalDescription,
            gender: this.state.gender
        };

        const updatedVolunteer = {
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            email: this.state.email,
            password: this.state.password,
            phoneNumber: this.state.phoneNumber,
            personalDescription: this.state.personalDescription,
            gender: this.state.gender,
            preferredType: this.state.preferredType.toUpperCase()
        }

        const token = this.state.currentUser.accessToken
        const email = this.state.currentUser.email
        const roles = this.state.currentUser.roles

        if (roles.includes("ROLE_ORGANIZATION")) {
            if (isValid) {
                updateOrganization(updatedOrganization, token, email, (organization, response) => {
                    if (organization) {
                        this.setState({
                            error: false,
                        })
                    } else {
                        this.setState({
                            error: true,
                            errorMsg: response
                        })
                    }
                });
                console.log('Sent!')
            } else {
                console.log("Client Side Form Validation error! ")
            }
        }

        if (roles.includes("ROLE_INDIVIDUAL")) {
            if (isValid) {
                updateIndividual(updatedIndividual, token, email, (organization, response) => {
                    if (organization) {
                        this.setState({
                            error: false,
                        })
                    } else {
                        this.setState({
                            error: true,
                            errorMsg: response
                        })
                    }
                });
                console.log('Sent!')
            } else {
                console.log("Client Side Form Validation error! ")
            }
        }

        if (roles.includes("ROLE_VOLUNTEER")) {
            if (isValid) {
                updateVolunteer(updatedVolunteer, token, email, (organization, response) => {
                    if (organization) {
                        this.setState({
                            error: false,
                        })
                    } else {
                        this.setState({
                            error: true,
                            errorMsg: response
                        })
                    }
                });
                console.log('Sent!')
            } else {
                console.log("Client Side Form Validation error! ")
            }
        }
    }

    render() {
        console.log('Objectt', this.state)
        const profileOrganization = (
            <ProfileOrganization
                contactFirstName={this.state.contactFirstName}
                contactLastName={this.state.contactLastName}
                organizationName={this.state.organizationName}
                email={this.state.email}
                password={this.state.password}
                phoneNumber={this.state.phoneNumber}
                street={this.state.street}
                callback={this.handleChange}
                houseNumber={this.state.houseNumber}
                city={this.state.city}
                zipCode={this.state.zipCode}
                disabled={this.state.disabled}
            />
        )

        const profileIndividual = (
            <ProfileIndividual
                firstName={this.state.firstName}
                lastName={this.state.lastName}
                email={this.state.email}
                password={this.state.password}
                phoneNumber={this.state.phoneNumber}
                street={this.state.street}
                callback={this.handleChange}
                houseNumber={this.state.houseNumber}
                city={this.state.city}
                zipCode={this.state.zipCode}
                gender={this.state.gender}
                disabled={this.state.disabled}
            />
        )

        const profileVolunteer = (
            <ProfileVolunteer
                firstName={this.state.firstName}
                lastName={this.state.lastName}
                email={this.state.email}
                password={this.state.password}
                phoneNumber={this.state.phoneNumber}
                callback={this.handleChange}
                gender={this.state.gender}
                preferredType={this.state.preferredType}
                disabled={this.state.disabled}
            />
        )

        const roles = this.state.currentUser.roles
        const profile = ((profileOrganization, profileVolunteer, profileIndividual) => {
            if (roles.includes("ROLE_ORGANIZATION")) {
                return profileOrganization;
            }
            if (roles.includes("ROLE_INDIVIDUAL")) {
                return profileIndividual
            }
            return profileVolunteer
        })

        const firstAndLastName = `${this.state.firstName} ${this.state.lastName}`;
        const isOrganizationOrIndividual = this.state.organizationName ? this.state.organizationName : firstAndLastName;
        return (<div className={styles.dashboard}>
            <div className={styles.headlineAndButton}>
                <div>
                    <h3 className={styles.h3}>Profile</h3>
                    <p>{isOrganizationOrIndividual} <Link className={profileStyles.a} to='/' onClick={this.logOut}>Log out</Link></p>
                </div>
                <button className={styles.button} onClick={this.handleClickEdit}>Edit Profile</button>
            </div>
            <section className={styles.opportunitiesWrapper}>
                <h4 className={styles.h4}>Your Data</h4>
                <section className={profileStyles.profile}>
                    <form action="http://localhost:8080/register/organization" autoComplete="off" method="POST" ref={this.form}>
                        <div className={profileStyles.profileWrapper}>
                            {profile(profileOrganization, profileVolunteer, profileIndividual)}
                        </div>
                        <label htmlFor="personalDescription">About
                        <textarea
                                id="personalDescription"
                                placeholder={this.state.personalDescription}
                                aria-label={this.state.personalDescription}
                                name="personalDescription"
                                value={this.state.personalDescription}
                                onChange={e => this.handleChange(e)}
                                disabled={this.state.disabled} />
                        </label>
                        <button type="submit"
                            className={styles.button} style={{ display: this.state.display }}
                            onClick={e => this.handleAddButtonClicked(e)}
                        >Save</button>
                    </form>
                </section>
            </section>
        </div>)
    }
}


