import React from 'react';
import styles from './OpportunityFullView.module.scss'
import { getOpportunity } from '../../Utils/networking'
import Header from '../Header'
import HeaderLoggedIn from '../Dashboard/HeaderLoggedIn'
import Footer from '../Footer'
import { Link } from 'react-router-dom'
import getImage from '../getImagesForCategory'
import AuthService from "../../services/auth.service";


export default class OpportunityFullView extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentUser: AuthService.getCurrentUser(),
            opportunity: '',
            error: '',
            loading: false,
            location: '',
            path:'',
            display: 'block'
        };
    }


    //DISPLAY IMAGES IN DETAILED VIEW
    componentDidMount() {
        this.setState({
            loading: true,
        });
        const id = this.props.match.params.id
        getOpportunity(id, (opportunity, responseMsg) => {
            if (opportunity) {
                this.setState({
                    opportunity: opportunity,
                    loading: false,
                });
                const image = getImage(this.state.opportunity.category);
                const imgPath = image[0].imgPath
                this.setState({
                    path: imgPath
                })
                return;
            }
            else {
                this.setState({
                    error: responseMsg,
                    loading: false
                });
                return;
            }
        });

        if (this.state.currentUser && (this.state.currentUser.roles.includes('ROLE_INDIVIDUAL') || this.state.currentUser.roles.includes('ROLE_ORGANIZATION'))) {
            this.setState({display: 'none'})
        }
    }


    render() {
        const isHeaderShown = this.state.currentUser ? <HeaderLoggedIn /> : <Header />
        return (<>
        {isHeaderShown}
            <div className={styles.wrapper}>
                <div className={styles.imgContainer}
                    style={{
                        width: '100%',
                        backgroundImage: `url(${this.state.path})`,
                        backgroundPosition: 'center',
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat'
                    }}>
                </div>
                <div>
                    <div className={styles.infoWrapper}>
                        <div>
                            <h3 className={styles.h3}>{this.state.opportunity.name}</h3>
                            <p className={styles.postedBy}>posted by <a className={styles.aLink} href="/">{this.state.opportunity.creatorName}</a></p>
                        </div>
                        <div>
                            <button className={styles.button} style={{ display: this.state.display }}>Apply</button>
                        </div>
                    </div>
                    <div className={styles.container}>
                        <div> <p><b>Where:</b> <br />{this.state.opportunity.zipCode}, {this.state.opportunity.city} </p></div>
                        <div>  <p><b>When:</b> <br />{this.state.opportunity.occurDate} / {this.state.opportunity.startTime}–{this.state.opportunity.endTime} </p></div>
                    </div>
                    <div>
                        <p className={styles.desc}>{this.state.opportunity.detailedDescription}</p>
                        <div>
                            <p className={styles.postedBy}>Category: {this.state.opportunity.category}</p>
                        </div>
                    </div>
                    <div className={styles.back}><br />
                   <Link to='/'><button className={styles.button}>Go Back</button></Link>
                   </div>
                </div>
            </div>
            <Footer />
        </>)
    }
}