import React from 'react';
import styles from '../../styles/opportunity.module.scss'
import { Link } from "react-router-dom";
import getImage from '../getImagesForCategory'

export default class Opportunity extends React.Component {

    render() {
        const image = getImage(this.props.category);
        const imgPath = image[0].imgPath
        const path = `/opportunities/${this.props.id}`;
        // const pathCreator = `/profile/${this.props.creatorId}`;
        return (<div className={styles.gridElement}>
            <div className={styles.imgWrapper}>
                <img className={styles.img} src={imgPath} alt="Volunteer Opportunity" />
              </div>  
              <div>
                <div>
                    <h4 className={styles.h4}>{this.props.name}</h4>
                    <p className={styles.postedBy}>posted by <Link className={styles.a} to='/'>{this.props.creator}</Link></p>
                </div>
                <div>
                    <p className={styles.desc}>{this.props.desc}</p>
                    <div>
                        <p className={styles.postedBy}>Participants: {this.props.totalNumberOfAccepted} / {this.props.numberOfParticipantsWanted}</p>
                        <p className={styles.postedBy}>Duration: {this.props.durationInMinutes} h</p>
                    </div>
                    <div className={styles.discover}>
                        <Link className={[styles.aDesc, styles.a].join(' ')} to={path}>Read More</Link>
                        <p className={styles.icon}>
                            <i className={[styles.arrow, styles.right].join(' ')}></i>
                        </p>
                    </div>
                </div>
            </div>
        </div>)
    }
}


