import React from 'react';
import Opportunity from '../Home/Opportunity'
import '../../styles/opportunities.scss'
import { getIndividual, getOrganization } from '../../Utils/networking'
import AuthService from "../../services/auth.service";
import { css } from "@emotion/core";
import BeatLoader from "react-spinners/ClipLoader";


export default class PostedOpportunities extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentUser: AuthService.getCurrentUser(),
            organizationName: '',
            createdOpportunities: [],
            firstName: '',
            lastName: '',
            error: '',
            loading: false
        };
    }

    componentDidMount() {
        this.setState({
            loading: true,
        });

        const token = this.state.currentUser.accessToken
        const email = this.state.currentUser.email

        getOrganization(token, email, (user, responseMsg) => {
            if (user) {
                this.setState({
                    organizationName: user.organizationName,
                    createdOpportunities: user.createdOpportunity
                });
                console.log('USER', user)
                return;
            }
            else {
                return;
            }
        });

        getIndividual(token, email, (user, responseMsg) => {
            if (user) {
                this.setState({
                    firstName: user.firstName,
                    lastName: user.lastName,
                    createdOpportunities: user.createdOpportunity
                });
                return;
            }
            else {

                return;
            }
        });

       
    }

    render() {
        //Spinner
        const override = css`
            display: block;
            margin: 0 auto;
            border-color: black;
            `;
        const opportunityList = this.state.createdOpportunities
            .map((opportunity) => {
                const durationInHours = opportunity.durationInMinutes/60;
                return <Opportunity 
                key={opportunity.id} 
                id={opportunity.id} 
                name={opportunity.name} 
                desc={opportunity.shortDescription} 
                creator={opportunity.creatorName} 
                category={opportunity.category}
                numberOfParticipantsWanted={opportunity.numberOfParticipants} 
                durationInMinutes={durationInHours}
                totalNumberOfAccepted={opportunity.acceptedVolunteers.length} 
                />;
            });

        const spinner = <BeatLoader
            css={override}
            size={150}
            color={"black"}
            loading={this.state.loading}
        />

        const isLoading = this.state.loading === true ? spinner : opportunityList

        return (
            <div className='gridDashboard'>
                {isLoading}
            </div>)
    }
}
