import React from 'react';
import oppstyles from '../../styles/opportunity.module.scss'
import getImage from '../getImagesForCategory'
import { Link } from 'react-router-dom'

export default class SuggestedOpportunity extends React.Component {
    render() {
        const prop = this.props
        // const image = getImage(this.props.category);
        // const imgPath = image[0].imgPath
        const path = `/opportunities/${this.props.id}`;
        return (<div className={oppstyles.gridElement}>
                <div>
                    <div className={oppstyles.headline}><h4 className={oppstyles.h4}>{prop.name}</h4></div>
                    <p className={oppstyles.postedBy}>posted by <a className={oppstyles.a} href="/">{prop.creator}</a></p>
                </div>
                <div>
                    <p className={oppstyles.desc}>{this.props.desc}</p>
                    <div>
                        <p className={oppstyles.postedBy}>Participants: {this.props.totalNumberOfAccepted} / {this.props.numberOfParticipantsWanted}</p>
                        <p className={oppstyles.postedBy}>Duration: {this.props.durationInMinutes} h</p>
                    </div>
                    <div className={oppstyles.discover}>
                        <Link className={[oppstyles.aDesc, oppstyles.a].join(' ')} to={path}>Read More</Link>
                        <p className={oppstyles.icon}>
                            <i className={[oppstyles.arrow, oppstyles.right].join(' ')}></i>
                        </p>
                    </div>
                </div>
            </div>)
    }
}