import React from 'react';
import UpcomingOpportunities from './UpcomingOpportunities'
import HeroOpportunities from './HeroOpportunities'
import SuggestedOpportunities from './SuggestedOpportunities'
import styles from './Dashboard.module.scss'
import { getUser } from '../../Utils/networking'
import AuthService from "../../services/auth.service";
import { Link } from 'react-router-dom'


export default class Dashboard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentUser: AuthService.getCurrentUser(),
            firstName: '',
        };
    }


    componentDidMount() {
        const token = this.state.currentUser.accessToken
        const email = this.state.currentUser.email


        getUser(token, email, (user, responseMsg) => {
            if (user) {
                this.setState({
                    firstName: user.firstName,
                    appliedOpportunities: user.appliedOpportunities,
                    acceptedOpportunities: user.acceptedOpportunities
                });
                return;
            }
            else {
                console.log("Get UpcomingOpportunities:  ERROR!")
                return;
            }
        });
    }


    render() {
        return (<div className={styles.dashboard}>
            <div className={styles.wrapper}>
                <div className={styles.rightSection}>
                    <h3 className={styles.h3}>Dashboard</h3>
                    <p>Welcome, {this.state.firstName}! <Link className={styles.a} to='/' onClick={this.logOut}>Log out</Link></p>
                </div>
                <section className={styles.opportunitiesWrapper}>
                <h4 className={styles.h4}>Your Upcoming Opportunities</h4>
                    <UpcomingOpportunities />
                </section>
                <section className={styles.opportunitiesWrapper}>
                <h4 className={styles.h4}>Hero Opportunities</h4>
                    <HeroOpportunities />
                </section>
                <section className={styles.opportunitiesWrapper}>
                <h4 className={styles.h4}>Your Suggested Opportunities</h4>
                    <SuggestedOpportunities />
                 </section>
            </div>
        </div>)
    }
}


