import React from 'react';
import SuggestedOpportunity from './SuggestedOpportunity'
import '../../styles/opportunities.scss'
import { getSuggestedOpportunities } from '../../Utils/networking'
import AuthService from "../../services/auth.service";
import { css } from "@emotion/core";
import BeatLoader from "react-spinners/ClipLoader";


export default class SuggestedOpportunities extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentUser: AuthService.getCurrentUser(),
            opportunityList: [],
            error: '',
            loading: false
        };
    }


    componentDidMount() {
        this.setState({
            loading: true,
        });

        const token = this.state.currentUser.accessToken
        const id = this.state.currentUser.id

        getSuggestedOpportunities(token, id, (opportunities, responseMsg) => {
            if (opportunities) {
                this.setState({
                    opportunityList: opportunities,
                    loading: false
                });
                return;
            }
            else {
                this.setState({
                    error: responseMsg,
                    loading: false
                });
                return;
            }
        });
    }

    render() {
        //Spinner
        const override = css`
            display: block;
            margin: 0 auto;
            border-color: black;
            `;

        const opportunityList = this.state.opportunityList
        .map((opportunity) => {
            const durationInHours = opportunity.durationInMinutes/60;
            return <SuggestedOpportunity 
            key={opportunity.id} 
            id={opportunity.id} 
            name={opportunity.name} 
            desc={opportunity.shortDescription} 
            creator={opportunity.creatorName} 
            category={opportunity.category}
            numberOfParticipantsWanted={opportunity.numberOfParticipants} 
            durationInMinutes={durationInHours}
            // creatorId={opportunity.creatorId}
            totalNumberOfAccepted={opportunity.acceptedVolunteers.length} />;
        });

        const spinner = <BeatLoader
        css={override}
        size={150}
        color={"black"}
        loading={this.state.loading}
      />

        const isLoading = this.state.loading === true ? spinner : opportunityList

        return (
                <div className='grid'>
                 {isLoading}
                </div>)
    }
}
