import React from 'react';
import RegistrationForm from './Components/Registration/RegistrationForm'
import Header from '../src/Components/Header'
import Footer from '../src/Components/Footer'
import Hero from './Components/Home/Hero'
import Opportunities from './Components/Home/Opportunities'
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import HeaderLoggedIn from './Components/Dashboard/HeaderLoggedIn'
import Dashboard from './Components/Dashboard/Dashboard'
import LogIn from './Components/LogIn/LogIn'
import AuthService from "./services/auth.service";
import SelectionTabsRegister from './Components/Registration/SelectionTabsRegister'
import RegistrationFormIndividual from './Components/Registration/RegistrationFormIndividual'
import RegistrationOrganization from './Components/Registration/RegistrationOrganisation'
import DashboardOrganization from './Components/Dashboard/DashboardOrganization'
import OpportunityFullView from './Components/Home/OpportunityFullView'
import CreateOpportunity from './Components/Dashboard/CreateOpportunity'
import ProfileVolunteer from './Components/Profile/ProfileVolunteer'
import ProfileOrganization from './Components/Profile/ProfileOrganization'
import SearchOpportunities from './Components/Opportunities/SearchOpportunities.js'


const index = () => {
  const currentUser = AuthService.getCurrentUser()
  if (currentUser) {
    return (<>
      <HeaderLoggedIn />
      <Hero />
      <Opportunities />
      <Footer />
    </>)
  } else {
    return (<>
      <Header />
      <Hero />
      <Opportunities />
      <Footer />
    </>)
  }
}

const registrationVolunteer = () => (
  <>
    <Header />
    <SelectionTabsRegister />
    <RegistrationForm />
    <Footer />
  </>
)

const registrationIndividual = () => (
  <>
    <Header />
    <SelectionTabsRegister />
    <RegistrationFormIndividual />
    <Footer />
  </>
)

const registrationOrganization = () => (
  <>
    <Header />
    <SelectionTabsRegister />
    <RegistrationOrganization />
    <Footer />
  </>
)

const logIn = () => (
  <>
    <Header />
    <LogIn />
    <Footer />
  </>
)

const createOpportunity = () => {
  const currentUser = AuthService.getCurrentUser();
  if (currentUser) {
    if (!currentUser.roles.includes("ROLE_VOLUNTEER")) {
    return (<>
      <HeaderLoggedIn />
      <CreateOpportunity />
      <Footer />
    </>)
  }
}
}

const dashboard = () => {
  const currentUser = AuthService.getCurrentUser();
  if (currentUser) {
    if (currentUser.roles.includes("ROLE_VOLUNTEER")) {
    return (<>
      <HeaderLoggedIn />
      <Dashboard />
      <Footer />
    </>)
    }
    return (<>
      <HeaderLoggedIn />
      <DashboardOrganization />
      <Footer />
    </>)
  }   
    return (<>
      <Header />
      <LogIn />
      <Footer />
    </>)
}

const profile = () => {
  const currentUser = AuthService.getCurrentUser();
  if (currentUser) {
    return (<>
      <HeaderLoggedIn />
      <ProfileOrganization />
      <Footer />
    </>)
  }   
    return (<>
      <Header />
      <LogIn />
      <Footer />
    </>)
}

//NEED NEW ENDPOINTS
const creatorPages = () => {
  const currentUser = AuthService.getCurrentUser();
  if (currentUser) {
    return (<>
      <HeaderLoggedIn />
      <ProfileOrganization />
      <Footer />
    </>)
  }   
    return (<>
      <Header />
      <ProfileOrganization />
      <Footer />
    </>)
}

const searchOpportunities = () => {
  const currentUser = AuthService.getCurrentUser()
  if (currentUser) {
    return (<>
      <HeaderLoggedIn />
      <SearchOpportunities />
      <Footer />
    </>)
  }   
    return (<>
      <Header />
      <ProfileOrganization />
      <Footer />
    </>)
}

const Routes = () => (
  <BrowserRouter>
    <Switch>
      <Route path='/' component={index} exact={true} />
      <Route path='/register-volunteer' component={registrationVolunteer} />
      <Route path='/register-individual' component={registrationIndividual} />
      <Route path='/register-organization' component={registrationOrganization} />
      <Route path='/login' component={logIn} />
      <Route path='/dashboard' component={dashboard} />
      <Route path='/opportunities/:id' component={OpportunityFullView} />
      <Route path='/create-opportunity' component={createOpportunity} />
      <Route path='/opportunities' component={searchOpportunities} />
      <Route path='/profile' component={profile} />
      <Route path='/profile/:id' component={creatorPages} />
    </Switch>
  </BrowserRouter>
);

export default Routes;