import React from 'react';
import styles from '../Header.module.scss'
import { Link } from 'react-router-dom'
import AuthService from "../../services/auth.service";

class HeaderLoggedIn extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentUser: AuthService.getCurrentUser(),
        };
        this.logOut = this.logOut.bind(this);
        
      }

    logOut() {
        AuthService.logout();
    }

    render() {
        return (
        <header className={styles.header}>
            <h1 className={styles.h1}><Link className={styles.a} to="/">Apptruistic</Link></h1>
            <nav>
            <a className={styles.a} href="/opportunities">Opportunities</a>
                <a className={styles.a} href="/profile">My Profile</a>
                <Link to="/dashboard"><button className={styles.buttonLogOut}>Dashboard</button></Link>
            </nav>
        </header>)
}
}

export default HeaderLoggedIn