import React from 'react';
import { addVolunteer } from '../../Utils/networking'
import styles from './RegistrationForm.module.scss'
import { ReactComponent as Illustation } from './Team-Work.svg';

export default class RegistrationForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            firstName: '',
            lastName: '',
            email: '',
            dateOfBirth: '',
            day: '',
            month: '',
            category: '',
            year: '',
            gender: '',
            password: '',
            preferredType: '',
            error: false,
            errorMsg: '',
            display: 'none',
            displayForm: 'block',
            phoneNumber: ''
        }

        this.form = React.createRef();
        this.handleChange = this.handleChange.bind(this);
        this.onAddVolunteer = this.onAddVolunteer.bind(this);
        this.validate = this.validate.bind(this);
    }

    //Form Validation API throws a true if it's valid and a 'false' if it's invalid
    validate() {
        return this.form.current.reportValidity();
    }

    handleChange(event) {
        if (event.target.id === 'preferredType') {
            this.setState({
                preferredType: event.target.value.toUpperCase()
            })
        } else {
            this.setState({
                [event.target.id]: event.target.value
            });
        }
        if (this.state.day && this.state.month && this.state.year) {
            const birthDateParsed = `${this.state.day}.${this.state.month}.${this.state.year}`;
            this.setState({ dateOfBirth: birthDateParsed })
        }
    };



    //OnSubmit:
    handleAddButtonClicked(e) {
        e.preventDefault();
        const typeToUpperCase = this.state.preferredType.toUpperCase()
        const categoryToUppercase = this.state.category.toUpperCase()
        const isValid = this.validate();
        const newUser = {
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            email: this.state.email,
            dateOfBirth: this.state.dateOfBirth,
            gender: this.state.gender,
            password: this.state.password,
            phoneNumber: this.state.phoneNumber,
            preferredType: typeToUpperCase,
            category: categoryToUppercase
        }
console.log('New User',newUser)

        if (isValid) {
            addVolunteer(newUser, (volunteerList, response) => {
                if (volunteerList) {
                    this.setState({
                        error: false,
                        display: 'block',
                        displayForm: 'none'
                    })
                } else {
                    this.setState({
                        error: true,
                        errorMsg: response
                    })
                }
            });

        } else {
            // this.setState({
            //     error: true,
            //     errorM
            // })
            //Take care of errors from the clients side.... with the validator??? check homework html5 form validation
            console.log("Client Side Form Validation error! ")
        }
    };



    onAddVolunteer(volunteer) {
        addVolunteer(volunteer);
    }

    render() {
        const takenEmailErrorMsg = this.state.errorMsg.includes("Email is already taken") ? "Email is already taken." : "Something went wrong, please try again."
        return (<div className={styles.page}>
            <div className={styles.formDiv}>
                <h3>Register <br />as volunteer</h3>
                <div className={styles.alert} style={{ display: this.state.display }}><p>Hello, {this.state.firstName}!</p>
                    <p>You have successfully signed up to Apptruistic! You can now log in with your email and password. Happy volunteering!</p></div>
                <form style={{ display: this.state.displayForm }} action="http://localhost:8080/volunteers" autoComplete="off" method="POST" ref={this.form}>
                    <div className={styles.formWrapper}>
                        <input
                            id="firstName"
                            placeholder='First Name*'
                            aria-label="First Name"
                            type="text"
                            name="firstName"
                            value={this.state.firstName}
                            onChange={this.handleChange}
                            required />
                        <input
                            id="lastName"
                            placeholder="Last Name*"
                            aria-label="Last Name"
                            type="text"
                            name="lastName"
                            value={this.state.lastName}
                            onChange={this.handleChange}
                            required />
                        <div className={styles.divider}>
                            <input
                                pattern="0[1-9]|[12][0-9]|3[01]"
                                id="day"
                                placeholder="DD"
                                aria-label="Day"
                                type="text"
                                name="day"
                                onChange={this.handleChange}
                            />
                            <input
                                pattern="0[1-9]|[12][0-12]"
                                id="month"
                                placeholder="MM"
                                aria-label="Month"
                                type="text"
                                name="month"
                                onChange={this.handleChange}
                            />
                            <input
                                className={styles.inputText}
                                id="year"
                                placeholder="YYYY"
                                aria-label="Year"
                                type="number"
                                name="year"
                                min="1900"
                                max="2002"
                                onChange={this.handleChange}
                            />
                        </div>
                        <input
                            id="phoneNumber"
                            placeholder="Phone number"
                            aria-label="Phone number"
                            type="text"
                            name="phoneNumber"
                            value={this.state.phoneNumber}
                            onChange={this.handleChange}
                        />
                        <input type="text" id="gender" name="fruit" list="genderList" required placeholder="Select your gender*"
                            pattern="[Ff]emale|[Mm]ale|[Uu]nspecified" onChange={this.handleChange} />
                        <datalist id="genderList">
                            <option>Female</option>
                            <option>Male</option>
                            <option>Unspecified</option>
                        </datalist>
                        <input
                            id="password"
                            placeholder="Password*"
                            aria-label="Password"
                            type="password"
                            name="password"
                            value={this.state.password}
                            onChange={this.handleChange}
                            minLength="8"
                            required />
                        <input
                            id="email"
                            placeholder="E-Mail*"
                            aria-label="E-Mail"
                            type="email"
                            name="email"
                            className={styles.email}
                            value={this.state.email}
                            onChange={this.handleChange}
                            required />

                        <input type="text" id="preferredType" name="preferredType" list="typeList" placeholder="Im interested in opportunities by an*:"
                            pattern="Individual|Organization|Both" onChange={this.handleChange} required />
                        <datalist id="typeList">
                            <option>Individual</option>
                            <option>Organization</option>
                            <option>Both</option>
                        </datalist>
                        <input className={styles.category} type="text" id="category" name="category" list="categoryList" required placeholder="Category*"
                            pattern="Household|Environment|Animals|Refugees|Caretaking|Transport|ITSupport|Teaching" onChange={this.handleChange} />
                        <datalist id="categoryList">
                            <option>Household</option>
                            <option>Environment</option>
                            <option>Animals</option>
                            <option>Refugees</option>
                            <option>Caretaking</option>
                            <option>Transport</option>
                            <option>ITSupport</option>
                            <option>Teaching</option>
                        </datalist>

                    </div>
                    {this.state.error === true ? <p className={styles.error}>{takenEmailErrorMsg}</p> : ""}
                    <button type="submit"
                        onClick={e => this.handleAddButtonClicked(e)}
                    >Sign up</button>
                </form>
            </div>
            <div className={styles.imgContainer}>
                <Illustation /></div>
        </div>
        );
    }
}

