export function addVolunteer(volunteer, completionHandler) {
	const request = createRequest("POST", `${baseURL}/register/volunteer`, completionHandler);
	request.setRequestHeader("Content-Type", "application/json");
	request.send(JSON.stringify(volunteer));
}

