function createRequest(method, url, completionHandler) {
	const request = new XMLHttpRequest();
	request.open(method, url);
	request.onload = () => {
		if (request.status >= 200 && request.status < 300) {
			console.log('YAS IT WORKED!', request.responseText)
			const response = JSON.parse(request.responseText);
			completionHandler(response, null)
		} else {
			console.log('Gacki request', request.responseText)
			completionHandler(null, request.responseText);
		}
	};
	request.onerror = () => {
		console.log('Error server down')
		completionHandler(null, 'Bad Server response: "' + request.statusText + '"');
		console.log(JSON.stringify(request.headers))
	}
	return request;
}