import React from 'react';
import UpcomingOpportunity from './UpcomingOpportunity'
import '../../styles/opportunities.scss'
import { getUser } from '../../Utils/networking'
import AuthService from "../../services/auth.service";
import { css } from "@emotion/core";
import BounceLoader from "react-spinners/ClipLoader";

export default class UpcomingOpportunities extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentUser: AuthService.getCurrentUser(),
            appliedOpportunities: [],
            acceptedOpportunities: [],
            error: '',
            loading: false
        };
    }

    componentDidMount() {
        this.setState({
            loading: true,
        });
        const token = this.state.currentUser.accessToken
        const email = this.state.currentUser.email

        getUser(token, email, (user, responseMsg) => {
            if (user) {
                this.setState({
                    appliedOpportunities: user.appliedOpportunities,
                    acceptedOpportunities: user.acceptedOpportunities,
                    loading: false,
                });
                return;
            }
            else {
                console.log("Get UpcomingOpportunities:  ERROR!")
                return;
            }
        });
    }

    render() {
        //Spinner
        const override = css`
            display: block;
            margin: 0 auto;
            border-color: black;
            `;

        const appliedOpportunitiesRender = this.state.appliedOpportunities
        .map((appliedOpportunity) => {
            return <UpcomingOpportunity 
            status="Applied"
            backgroundColor="yellow"
            key={appliedOpportunity.id}  
            name={appliedOpportunity.name} 
            desc={appliedOpportunity.description} 
            creator={appliedOpportunity.creatorName} 
            date={appliedOpportunity.occurDate} 
            location={appliedOpportunity.location}
            startTime={appliedOpportunity.startTime}
            endTime={appliedOpportunity.endTime}
            numberOfParticipantsWanted={appliedOpportunity.numberOfParticipants}
            totalNumberOfAccepted={appliedOpportunity.acceptedVolunteers.length}
            />;
        });

        const acceptedOpportunitiesRender = this.state.acceptedOpportunities
        .map((acceptedOpportunity) => {
            return <UpcomingOpportunity 
            status="Accepted"
            backgroundColor="#84D0AB"
            key={acceptedOpportunity.id}  
            name={acceptedOpportunity.name} 
            desc={acceptedOpportunity.description} 
            creator={acceptedOpportunity.creatorName} 
            date={acceptedOpportunity.occurDate} 
            location={acceptedOpportunity.location}
            startTime={acceptedOpportunity.startTime}
            endTime={acceptedOpportunity.endTime}
            numberOfParticipantsWanted={acceptedOpportunity.numberOfParticipants}
            totalNumberOfAccepted={acceptedOpportunity.acceptedVolunteers.length}
            />;
        });

        const listOfOpportunitiesTotal = [acceptedOpportunitiesRender, appliedOpportunitiesRender]

        const spinner = <BounceLoader
        css={override}
        size={150}
        color={"black"}
        loading={this.state.loading}
      />

        const isLoading = this.state.loading === true ? spinner : listOfOpportunitiesTotal

        return (
                <div className='grid'>
                 {isLoading}

                </div>)
    }
}
