import React from 'react'
import Opportunity from '../Home/Opportunity'
import '../../styles/opportunities.scss'
import { getOpportunities } from '../../Utils/networking'
import { css } from "@emotion/core";
import BeatLoader from "react-spinners/ClipLoader";
import { isThisSecond } from 'date-fns';

export default class SearchOpportunities extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            opportunityList: [],
            error: '',
            loading: false,
            numberOfOpportunities: 8,
            displayButton: 'block',
            displayText: 'none'
        };
        this.handleClick = this.handleClick.bind(this);
    }

    componentDidMount() {
        this.setState({
            loading: true,
        });
        getOpportunities((opportunities, responseMsg) => {
            if (opportunities) {
                this.setState({
                    opportunityList: opportunities,
                    loading: false
                });
                return;
            }
            else {
                this.setState({
                    error: responseMsg,
                    loading: false
                });
                return;
            }
        });
    }

    handleClick(e) {
        e.preventDefault();
        const opportunityListLength = this.state.opportunityList.length;
        if (this.state.numberOfOpportunities < opportunityListLength) {
            this.setState(prevState => ({
                numberOfOpportunities: prevState.numberOfOpportunities + 4
            }));
        } 
        console.log(this.state.numberOfOpportunities)
    }



     componentDidUpdate(prevProps, prevState) {
        console.log('Component did update!')
        if ((prevState.numberOfOpportunities !== this.state.numberOfOpportunities) && (this.state.numberOfOpportunities >= this.state.opportunityList.length)) {
            this.setState({
                displayButton: 'none',
                displayText: 'block'
            })
        }
     }

    render() {
        //Spinner
        const override = css`
            display: block;
            margin: 0 auto;
            border-color: black;
            `;

        const opportunityList = this.state.opportunityList
            .slice(0, this.state.numberOfOpportunities)
            .map((opportunity) => {
                const durationInHours = opportunity.durationInMinutes / 60;
                return <Opportunity
                    key={opportunity.id}
                    id={opportunity.id}
                    name={opportunity.name}
                    desc={opportunity.shortDescription}
                    creator={opportunity.creatorName}
                    category={opportunity.category}
                    numberOfParticipantsWanted={opportunity.numberOfParticipants}
                    durationInMinutes={durationInHours}
                    // creatorId={opportunity.creatorId}
                    totalNumberOfAccepted={opportunity.acceptedVolunteers.length} />;
            });

        const spinner = <BeatLoader
            css={override}
            size={150}
            color={"black"}
            loading={this.state.loading}
        />

        const isLoading = this.state.loading === true ? spinner : opportunityList
        const isNotEmpty = this.state.error === '' ? isLoading : this.state.error;

        //To do: what to render when there are no opportunities?
        return (<section className='opportunities'>
            <div className='opportunitiesTitle'>
                <h3>Find Opportunities</h3>
                <div className='searchBar'>

                </div>
            </div>
            <div className='grid'>
                {isNotEmpty}
            </div>
            <div className='buttonWrapper'>
                <p className='p' style={{ display: this.state.displayText }}>You have reached the end of the list!</p>
                <button className='button' style={{ display: this.state.displayButton }} onClick={this.handleClick}>Load more</button>
            </div>
        </section>)
    }


}
